body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: #e0e0e0;
  margin: 0;
  padding: 0;
}

html {
  padding: 0;
  margin: 0;
}





input,
button {
  margin: 5px;
  padding: 8px;
  font-size: 16px;
  background-color: #333;
  color: #e0e0e0;
  border: 1px solid #555;
  border-radius: 5px;
  cursor: pointer;
}

.link-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #1e1e1e;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
}

.link-item a {
  text-decoration: none;
  color: #00ff15;
}

.delete-btn {
  background: #d32f2f;
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  border-radius: 3px;
}

.delete-btn:hover {
  background: #b71c1c;
}

td {
  width: 50px;
  height: 20px;
  text-align: center;
  position: relative;
  background-color: #1e1e1e;
  color: #e0e0e0;
}

.empty_progress {
  background-color: #333;
  width: 100px;
  height: 20px;
  border: 1px solid #555;
}

.progress {
  background-color: #4caf50;
  width: 100px;
  height: 20px;
  border: 1px solid #555;
}

.progress-cell {
  width: 100%;
  height: 30px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  overflow: hidden;
  position: relative;
  border: 1px solid #555;
  background-color: #1e1e1e;
}

.progress-fill {
  background-color: #00ff15;
  height: 100%;
}

.main_wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

@media (max-width: 768px) {
  .main_wrap {
    grid-template-columns: 1fr;
  }
}

.timers {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

#progressGrid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 5px;
  width: 100%;
  padding-top: 20px;
}

.timerContainer_wrap {
  padding: 10px;
}

.light_green {
  color: #00ff15;
}

.progressBar_green_boxes_wrap {
