/* GoalsList.css */

/* Общий контейнер для цели */
.goal-container {
  border: 1px solid #444;
  max-width: 400px;
  /* Темная граница */
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  background-color: #222;
  /* Темный фон */
  color: #fff;
  /* Белый текст */
}

.goal-container-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

/* Заголовок цели */
.goal-container-header h3 {
  /* margin: 0 0 5px 0; */
  margin: 0;
  font-size: 18px;
  color: #00ff15;
  /* Ярко-зеленый заголовок */
}

/* Цена цели */
.goal-container p {
  margin: 0 0 5px 0;
  font-size: 14px;
  color: #ccc;
  /* Светло-серый текст */
}

/* Контейнер для прогресс-бара */
.progress-bar-container {
  width: 100%;
  height: 20px;
  background-color: #333;
  /* Темный фон прогресс-бара */
  border-radius: 5px;
  overflow: hidden;
  margin-top: 5px;
}

/* Прогресс-бар */
.progress-bar {
  height: 100%;
  background-color: #00ff15;
  /* Ярко-зеленый цвет прогресса */
  transition: width 0.3s ease;
  /* Плавная анимация */
}

/* Дополнительные стили для индикации прогресса */
.progress-bar-low {
  background-color: #ff4444;
  /* Красный для низкого прогресса */
}

.progress-bar-medium {
  background-color: #ffeb3b;
  /* Желтый для среднего прогресса */
}

.progress-bar-high {
  background-color: #00ff15;
  /* Ярко-зеленый для высокого прогресса */
}


/* Кнопка удаления */
.delete-goal-button {
  /* background-color: #ff4444; */
  /* Красный фон */
  color: #fff;
  margin-left: 20px;
  /* Белый текст */
  border: none;
  /* Без границы */
  padding: 5px 10px;
  /* Внутренние отступы */
  border-radius: 5px;
  /* Закругленные углы */
  cursor: pointer;
  /* Курсор указывает на кликабельность */
  font-size: 14px;
  /* Размер шрифта */
  /* margin-top: 10px; */
  /* Отступ сверху */
  transition: background-color 0.3s ease;
  /* Плавное изменение цвета */
}

.delete-goal-button:hover {
  background-color: #cc0000;
  /* Темно-красный при наведении */
}